                                        <span v-if="balance == 0">0.00</span>
                                    </span>
                                </h4>
                                <p class="text-muted mb-0">Your Balance</p>
                            </div>
                            <p class="text-muted mt-3 mb-0" >
                                <span class="text-success me-1" v-if="balance != null && balance != 0">
                                    {{ balance.toFixed(2) }}
                                </span>
                                <span class="text-success me-1" v-else>
                                    0.00
                                </span>
                                FatCake Tokens
                            </p>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://raw.githubusercontent.com/ApeSwapFinance/apeswap-token-lists/main/assets/CAKE.svg" height="80" alt="">
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="rewards" :duration="2000" :decimals="5"></countTo>
                                    </span>
                                    CAKE
                                </h4>
                                <p class="text-muted mb-0">Total Rewards</p>
                            </div>
                            <p class="text-success mt-3 mb-0">
                                <i class="mdi mdi-currency-usd"></i>
                                {{ (rewards * cakePrice).toFixed(5) }}
                            </p>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://raw.githubusercontent.com/ApeSwapFinance/apeswap-token-lists/main/assets/CAKE.svg" height="80" alt="">
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="nextRewards" :duration="2000" :decimals="5"></countTo>
                                    </span>
                                    CAKE
                                </h4>
                                <p class="text-muted mb-0">Next Payout Rewards</p>
                            </div>
                            <p class="text-success mt-3 mb-0">
                                <i class="mdi mdi-currency-usd"></i>
                                {{ (nextRewards * cakePrice).toFixed(5) }}
                            </p>
                        </div>
                    </div>
                </div>
                <div class="col-md-6 col-xl-3">
                    <div class="card">
                        <div class="card-body">
                            <div class="float-end mt-2">
                                <img src="https://raw.githubusercontent.com/ApeSwapFinance/apeswap-token-lists/main/assets/CAKE.svg" height="80" alt="">
                            </div>
                            <div>
                                <h4 class="mb-1 mt-1">
                                    $
                                    <span data-plugin="counterup">
                                        <countTo :startVal="0" :endVal="cakePrice" :duration="2000" :decimals="2"></countTo>
                                    </span>
                                </h4>
                                <p class="text-muted mb-0">Cake Price</p>
                            </div>
                            <p class="text-muted mt-3 mb-0" v-if="cakeChange">
                                <span class="text-success me-1" v-if="cakeChange >= 0">
                                    <i class="mdi mdi-arrow-up-bold me-1"></i>
                                    {{ cakeChange.toFixed(2) }} %
                                </span>

                                <span class="text-danger me-1" v-if="cakeChange < 0">
                                    <i class="mdi mdi-arrow-down-bold me-1"></i>
                                    {{ cakeChange.toFixed(2) }} %
                                </span>
                                since last 24h
                            </p>
                        </div>
                    </div>
                </div>
                <!-- end col-->
                <!-- end col-->
            </div>
            <div class="row" v-else>
                <div class="col-12 col-md-12 col-lg-8 col-xl-5">
                    <div class="card">
                        <div class="card-body">
                            <h4 class="card-title">FatCake Rewards Tracking</h4>
                            <p class="card-title-desc">
                            Easily rewards checking
                            FatCake Tracking App
                            FatCake's chart, price movements, market cap, volume and more for all. Track Frictionless Rewards of FatCake and it's value in USD.
                            </p>

                            <div class="">
                                <b-alert variant="danger" show>Not connected to any wallet!</b-alert
                                >
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            
            <br/>

            <!-- Estimated Rewards -->
            <div class="row" v-if="estimation.validBalance && $store.getters['wallet/validWallet']">
                <div class="col-12 col-md-12 col-lg-8 col-xl-5">
                    <h5>Estimated Rewards</h5>
                        
                    <div class="card text-center">
                        <div class="card-body no-border">
                            <b-dropdown
                                class="float-end"
                                variant="white"
                                menu-class="dropdown-menu-end"
                                right
                                toggle-class="font-size-16 text-body p-0"
                            >
                            <template v-slot:button-content>
                                <i class="uil uil-ellipsis-h"></i>
                            </template>
                                <a class="dropdown-item" @click="changeVolume(0)">Current Volume</a>
                                <a class="dropdown-item" @click="changeVolume(200000)">$ 200,000</a>
                                <a class="dropdown-item" @click="changeVolume(500000)">$ 500,000</a>
                                <a class="dropdown-item" @click="changeVolume(1000000)">$ 1,000,000</a>
                            </b-dropdown>
                            <div class="clearfix"></div>
                            <div class="mb-4">
                            <img
                                src="https://i.ibb.co/c2Pqr4N/cherry-2.png"
                                alt
                                class="avatar-lg rounded-circle img-thumbnail"
                                style="background-color: transparent !important; border: none !important;"
                                v-if="estimation.estimatedRewards"
                            />
                            <b-spinner
                                class="m-2"
                                variant="primary"
                                role="status"
                                v-else
                            ></b-spinner>
                            </div>
                            <h5 class="font-size-16 mb-1" v-if="estimation.estimatedRewards">
                                <a href="#" class="text-dark">$ {{ estimation.estimatedRewards.toFixed(4) }}</a>
                            </h5>
                            <h5 class="font-size-16 mb-1" v-else>
                                <a href="#" class="text-dark">Calculating..</a>
                            </h5>
                            <p class="text-muted mb-2" v-if="estimation.volume == 0">Calculated on current volume and current CAKE price</p>
                            <p class="text-muted mb-2" v-else>Calculated on <span style="color: #e81e63;">$ {{ estimation.volume.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}</span> volume and current CAKE price</p>
                        </div>

                        <div class="btn-group" role="group">
                            <button type="button" class="btn btn-outline-light text-truncate" @click="changeRewardsDuration(1, estimation.volume)">
                                <div v-if="estimation.duration == 1">
                                    <i class="uil uil-check" style="color: #34a75c;"></i> 
                                    <span style="color: #34a75c;">Daily</span>
                                </div>
                                <div v-else>
                                    <i class="uil uil-calendar-alt"></i> 
                                    <span>Daily</span>
                                </div>
                            </button>
                            <button type="button" class="btn btn-outline-light text-truncate" @click="changeRewardsDuration(7, estimation.volume)">
                                <div v-if="estimation.duration == 7">
                                    <i class="uil uil-check" style="color: #34a75c;"></i> 
                                    <span style="color: #34a75c;">Weekly</span>
                                </div>
                                <div v-else>
                                    <i class="uil uil-calendar-alt" style="padding-right: 5px;"></i> 
                                    <span>Weekly</span>
                                </div>
                            </button>
                            <button type="button" class="btn btn-outline-light text-truncate" @click="changeRewardsDuration(30, estimation.volume)">
                                <div v-if="estimation.duration == 30">
                                    <i class="uil uil-check" style="color: #34a75c;"></i> 
                                    <span style="color: #34a75c;">Monthly</span>
                                </div>
                                <div v-else>
                                    <i class="uil uil-calendar-alt" style="padding-right: 5px;"></i> 
                                    <span>Monthly</span>
                                </div>
                            </button>
                            <button type="button" class="btn btn-outline-light text-truncate" @click="changeRewardsDuration(365, estimation.volume)">
                                <div v-if="estimation.duration == 365">
                                    <i class="uil uil-check" style="color: #34a75c;"></i> 
                                    <span style="color: #34a75c;">Annual</span>
                                </div>
                                <div v-else>
                                    <i class="uil uil-calendar-alt" style="padding-right: 5px;"></i> 
                                    <span>Annual</span>
                                </div>
                            </button>
                        </div>
                    </div>
                </div>
                <div class="col-12 col-md-12 col-lg-8 col-xl-5">
                    <br class="d-block d-lg-none" />
                    <h5>Claim Rewards</h5>
                        
                    <div class="card text-center">
                        <div class="card-body no-border">
                            <b-dropdown
                                class="float-end"
                                variant="white"
                                menu-class="dropdown-menu-end"
                                right
                                toggle-class="font-size-16 text-body p-0"
                            >
                            <template v-slot:button-content>
                                <i class="uil uil-ellipsis-h" style="color: transparent !important;"></i>
                            </template>
                            </b-dropdown>
                            <div class="clearfix"></div>
                            <div class="mb-4">
                            <img
                                src="https://i.ibb.co/HXbXGFf/IMG-6416.png"
                                alt
                                class="avatar-lg rounded-circle img-thumbnail"
                                style="background-color: transparent !important; border: none !important;"
                                v-if="!claimLoading && nextRewards >= 0"
                            />
                            <b-spinner
                                class="m-2"
                                variant="primary"
                                role="status"
                                v-else
                            ></b-spinner>
                            </div>
                            <h5 class="font-size-16 mb-1" v-if="nextRewards != null && nextRewards != 0">
                                <a href="#" class="text-dark">$ {{ (nextRewards * cakePrice).toFixed(5) }}</a>
                            </h5>
                            <h5 class="font-size-16 mb-1" v-if="nextRewards == null || nextRewards == 0">
                                No claimable rewards
                            </h5>
                            <p class="text-muted mb-2" v-if="nextRewards && nextRewards != 0">{{ nextRewards.toFixed(5) }} CAKE tokens ready to be claimed</p>
                            <p class="text-muted mb-2" v-if="nextRewards == null || nextRewards == 0">&nbsp;</p>
                        </div>

                        <div class="btn-group" role="group">
                            <button type="button" class="btn btn-outline-light text-truncate" disabled
                                v-if="nextRewards == null || nextRewards == 0 || claimLoading"
                            >
                                <div>
                                    <i class="uil uil-calendar-alt"></i> 
                                    <span>CLAIM</span>
                                </div>
                            </button>
                            <button type="button" class="btn btn-outline-light text-truncate" style="background-color: #e81e63 !important;" @click="claimRewards"
                                v-else
                            >
                                <div>
                                    <i class="uil uil-wallet" style="padding-right: 5px;"></i> 
                                    <span>CLAIM</span>
                                </div>
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    </div>
</template>

<script>
import Layout from "../../layouts/main";
import PageHeader from "@/components/page-header";
import appConfig from "@/app.config";
import countTo from "vue-count-to";
var Contract = require('web3-eth-contract');
import axios from "axios";
import Swal from "sweetalert2";

export default {
    page: {
        title: "Tracker",
        meta: [
            {
                name: "description",
                content: appConfig.description,
            },
        ],
    },
    components: {
        Layout,
        PageHeader,
        countTo
    },
    mounted: async function() {
        this.initPage();
    },
    methods: {
        claimRewards: async function() {
            this.claimLoading = true;
            var Web3 = require('web3');
            Contract.setProvider(Web3.givenProvider);
            let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';
            let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);
            let result = await window.ethereum.send('eth_requestAccounts');
            this.connectedWallet = result.result[0];
            
            try {
                await contract.methods.claim().send({
                    from: result.result[0]
                });

                Swal.fire("Claimed!", "Rewards successfully withdrawn to your wallet!", "success");
                this.initPage();
            } catch (error) {
                console.log("Abgelehnt");
                console.log(error);
            }
            this.claimLoading = false;
        },
        initPage: async function() {
            this.estimation =  {
                validBalance: true,
                volume: 0,
                duration: 1,
                estimatedRewards: null,
            };

            this.price = 0;
            this.cakePrice = 0;
            this.cakeChange = 0;
            this.balance = null;
            this.nextRewards = null;
            this.rewards = null;

            this.price = await this.getPrice();
            let cakeData = await this.getCakeData();
            this.cakePrice = cakeData.price;
            this.cakeChange = cakeData.priceChange;
            this.balance = await this.getBalance();
            
            this.rewards = await this.getAllRewards();
            this.nextRewards = await this.getNextPayoutRewards();

            this.estimation.estimatedRewards = await this.getEstimatedRewards(this.estimation.duration);
        },
        changeVolume: function(data) {
            this.estimation.volume = data;
            this.changeRewardsDuration(this.estimation.duration, data);
        },
        // Duration = Days
        async changeRewardsDuration(duration, volume) {
            this.estimation.estimatedRewards = null;
            this.estimation.duration = duration;
            this.estimation.estimatedRewards = await this.getEstimatedRewards(this.estimation.duration, volume);
        },
        async getEstimatedRewards(duration, volumeValue) {
            if (this.balance == null || this.balance == 0) {
                this.estimation.validBalance = false;
                return null;
            }

            let result = await axios.get("https://api.coingecko.com/api/v3/simple/price?ids=fatcake&vs_currencies=usd&include_market_cap=true&include_24hr_vol=true&include_24hr_change=true");

            let volume = (volumeValue === undefined || volumeValue == null || volumeValue == 0) ? result.data.fatcake.usd_24h_vol : volumeValue;

            let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';
            let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);

            let decimals = await contract.methods.decimals().call();
            let totalSupply = await contract.methods.totalSupply().call() / Math.pow(10, decimals);
            let burntSupply = await contract.methods.balanceOf("0x000000000000000000000000000000000000dead").call() / Math.pow(10, decimals);

            let circulatingSupply = totalSupply - burntSupply;
            let share = this.balance / circulatingSupply;

            return volume * 0.1 * share * duration;
        },
        getAllRewards: async function() {
            if (this.balance <= 0) return null;
            Contract.setProvider("https://bsc-dataseed.binance.org");
            let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';

            let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);
            let decimals = await contract.methods.decimals().call();
            let info = await contract.methods.getAccountDividendsInfo(this.$store.state.wallet.currentWallet).call();
            // console.log(info);
            return info[4] / Math.pow(10, decimals)
        },
        getNextPayoutRewards: async function() {
            if (this.balance <= 0) return null;
            Contract.setProvider("https://bsc-dataseed.binance.org");
            let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';

            let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);
            let decimals = await contract.methods.decimals().call();
            let info = await contract.methods.getAccountDividendsInfo(this.$store.state.wallet.currentWallet).call();
            // console.log(info);
            return info[3] / Math.pow(10, decimals)
        },
        getBalance: async function() {
            // set provider for all later instances to use
            Contract.setProvider("https://bsc-dataseed.binance.org");
            let contractAddress = '0xb7dba4c673bedb174dc3ff7ec65d17c863d39b16';

            let contract = new Contract(JSON.parse(this.contractAbi), contractAddress);
            let decimals = await contract.methods.decimals().call();
            
            try {
                let result = await contract.methods.balanceOf(this.$store.state.wallet.currentWallet).call();
                let balance = parseFloat(result) / Math.pow(10, decimals);
                return balance;
            } catch (error) {
                console.log(error);
                return 0;
            }
        },
        getPrice: async function() {
            let result = await axios.get("https://api.coingecko.com/api/v3/simple/price?ids=fatcake&vs_currencies=usd");
            return result.data.fatcake.usd;
        },
        getCakeData: async function() {
            let result = await axios.get("https://api.coingecko.com/api/v3/simple/price?ids=pancakeswap-token&vs_currencies=usd&include_24hr_change=true");
            return {
                price: result.data['pancakeswap-token'].usd,
                priceChange: result.data['pancakeswap-token'].usd_24h_change,
            };
        },
    },
    data: function() {
        return {
            estimation: {
                validBalance: true,
                volume: 0,
                duration: 1,
                estimatedRewards: null,
            },
            claimLoading: false,
            balance: null,
            price: 0,
            cakePrice: 0,
            cakeChange: 0,
            rewards: null,
            nextRewards: null,
            title: "Tracker",
            items: [
                {
                    text: "FatCake",
                    active: true,
                },
                {
                    text: "Tracker",
                },
            ],
            contractAbi: '[{"inputs":[],"stateMutability":"nonpayable","type":"constructor"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"owner","type":"address"},{"indexed":true,"internalType":"address","name":"spender","type":"address"},{"indexed":false,"internalType":"uint256","name":"value","type":"uint256"}],"name":"Approval","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"account","type":"address"},{"indexed":false,"internalType":"bool","name":"isExcluded","type":"bool"}],"name":"ExcludeFromFees","type":"event"},{"anonymous":false,"inputs":[{"indexed":false,"internalType":"address[]","name":"accounts","type":"address[]"},{"indexed":false,"internalType":"bool","name":"isExcluded","type":"bool"}],"name":"ExcludeMultipleAccountsFromFees","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"uint256","name":"newValue","type":"uint256"},{"indexed":true,"internalType":"uint256","name":"oldValue","type":"uint256"}],"name":"GasForProcessingUpdated","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"newLiquidityWallet","type":"address"},{"indexed":true,"internalType":"address","name":"oldLiquidityWallet","type":"address"}],"name":"LiquidityWalletUpdated","type":"event"},{"anonymous":false,"inputs":[{"indexed":true,"internalType":"address","name":"previousOwner","type":"address"},{"indexed":true,"internalType":"address","name":"newOwner","type":"address"}],"name":"OwnershipTransferred","type":"event"},{"anonymous":false,"inputs":[{"indexed":false,"internalType":"uint256","name":"iterations","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"claims","type":"uint256"},{"indexed":false,"internalType":"uint256","name":"lastProcessedIndex","type":"uint256"},{"indexed":true,"internalType":"bool","name":"automatic","type":"bool"},{"indexed":false,"internalType":"uint256","name":"gas","type":"uint256"},{"indexed":true,